.pointer{
    cursor: pointer;
}

.vertical-center{
    padding-top: 160px;
}

.panel{
    padding: 10px 10px 0 10px;

}

#dashboard-panel{
    padding: 0 10px 0 10px;
}

/*css for the display panel, display row, display element inside main panel*/

.display-panel{
    padding: 50px 100px 0 100px;
    font-size: 15px;
}
.display-row{
    padding-bottom: 25px;
}

.display-element{
    background-color: #E9EBEE;
    border-left: 10px solid white;
}

/*to bold display text*/
.bold{
    font-weight: bolder;
}

/*calendar month and year text color*/

.ui-datepicker select.ui-datepicker-month, .ui-datepicker select.ui-datepicker-year{
    color: black;
}

em{
    color: red;
}

/*textarea format*/

.no-v-resize {

    resize: none;
}

/*hide element form printing*/

@media print
{
    .no-print, .no-print *
    {
        display: none !important;
    }
}
@page {
    size: auto;
    margin: 0;
}

.print:last-child {
    page-break-after: auto;
}

/*css for a loading ajax request*/

#wait{
    display:none;
    width:69px;
    height:89px;
    border:0;
    position:absolute;
    top:50%;
    left:50%;
    padding:2px 0 20px 0;
}

.zero-padding-margin{
    padding: 0 0 0 0;
    margin: 0 0 0 0;
}

/*instruction table at home page*/

.dotted-border{
    border-left: dashed;
    border-width: 1px;
}
